
.lookbook {
  

  &-block {
    margin-top: 50px;
    min-height: 520px;
    height: auto; 
    color: #fff;
    position: relative;
  }  
  

  
  &-block h2 {
    font-family: @title-font;  
    font-size: 50px;
    line-height: 40px;    
    font-weight: 500;  
    display: inline-block;
    margin-bottom: 8px;
    color: #fff;
  }
  
  &-block p {
    font-family: @base-font;
    line-height: 25px;    
    color: #fff;
    font-size: 14px;    
    display: inline-block;
    padding: 0;
    margin-bottom: 30px;
  } 
  
  &-text.btn {
    color: #fff;
  }
  
  &-text a.btn {
    color: #fff;
  }
 
  &-text {
   width: 530px;  
   position: absolute;  
   min-height: 315px;
   box-sizing: border-box; 
   background-color: rgba(14,17,17, 0.8); 
   padding-left: 65px;  
   padding-right: 80px;
   padding-top: 50px;
   padding-bottom: 35px; 
   right: 60px;  
   top: 170px;  
  }
  
  &-text.second {
    left: 60px;
  }
 

}
 
@media (min-width: 993px ) and (max-width: 1200px) {
  .lookbook-text {
    top: 80px;
  }
}

@media screen and (max-width: 992px) {
  .lookbook {
    &-block {
     margin-left: -15px;
     width: 100%;
     height: auto;  
     margin-top: 24px;  
     margin-bottom: 24px;  
     background-size: 100% auto; 
     position: relative;  
    }
    
    &-text {
      width: 100%;
      position: static;
      height: auto;
      margin-top: -10px;
    }
    
    &-text h2 {
      margin-left: 0;
    }

  } 
}