.form {
  & .btns {
    margin-top: 30px;
  }
  & .btn-form {
    text-align: center;
    float: right;
    color: white;
    text-transform: none;
  }
  & label, & p {
    font-size: 15px;
    line-height: 20px;
    color: #768186;
    margin: 0;
    margin-bottom: 5px;
    & span {
      color: @blue;
      padding-left: 5px;

    }
  }
  & input {
    margin-top: 4px;
    margin-bottom: 23px;
    display: block;
    width: 100%;
    &.modal-input:first-child {
      margin-bottom: 20px;
    }
  }
  & textarea {
    margin-top: 4px;
    margin-bottom: 14px;
    display: block;
    width: 100%;
    height: 120px;
  }
  & .checkbox__text {
    padding: 0 0 0 39px;
    cursor: pointer;
    font-size: 14px;
    color: @navy;
    & a {
      color: @blue;
      text-decoration: underline;
    }
    &:before {
      width: 30px;
      height: 30px;
    }
  }
}

.service-form
{
  background: #a8dff8;
  padding: 30px 50px 40px;
  margin-top: 41px;
  margin-bottom: 50px;
  & input,  & textarea
  {
    background: #b9e5f9;
    border:1px solid white;
    margin-bottom: 12px;
  }
  &__title
  {
    color: #1e2528;
    font-size: 28px;
    margin-bottom: 28px;
  }
  & .checkbox__text {
    padding: 0 0 0 50px;
    cursor: pointer;
    font-size: 15px;
    color: #1e2528;
    & a {
      color: white;
      text-decoration: underline;
    }
    &:before {
      width: 40px;
      height: 40px;
      background: #b9e5f9;
      border:1px solid white;
    }
  }
  & .btns {
    margin-top: 15px;
  }
  & .btn-form
  {
    padding: 10px 27px 10px;
    position: relative;
    font-weight: 400;
    top: -8px;
  }
}