
.navbar {
  padding: 10px 0 ;
  position: relative;
  transition: 0.5s;
  margin-bottom:0;
  background: @black;
  //z-index: 24;

  &.zfixed
  {
    z-index: 24;
  }
  & .mobile-close
  {
    display: none;
    position: absolute;
    right:20px;
    top:20px;
  }
  & .add-menu
  {
    display: inline-block;
    & .lang-menu
    {
      display: inline-block;
      position: relative;
      top: -2px;
      & li
      {
        display: inline-block;
        color:@pink;
        font-weight: 900;
        font-size: 12px;
        margin-right: 5px;
        &.active
        {
          color:white;
        }
      }
    }
    & .cart-icon
    {
      display: inline-block;
      position: relative;
      top: 20px;
      right: -14px;
    }
  }
  &.active {
    background: #f6f6f6;
    box-shadow: 0px 5px 18px 4px #e5e5e5;
  }
  & .logo
  {
    display: inline-block;
  }
  & .soc-icons
  {
    display: inline-block;
    margin-left: 41px;
      & li
      {
        display: inline-block;
        margin-right: 10px;
        &.vk-icon
        {
          position: relative;
          top: -3px;
        }
      }
    
  }
  & .menu-block
  {
z-index: 1;
    //text-align: center;
  }
  & .mobile-menu {
    display: inline-block;

  }
  & .header-menu {
    display: inline-block;
    margin-left: 40px;
    margin-right: 52px;

    & li {
      color: @pink;
      font-size: 12px;
      display: inline-block;
      text-transform: uppercase;
      margin-right: 52px;
      transition: 0.2s;
      position: relative;
      text-shadow: 0 0 5px @pink;

      &:hover {
        color: white;
        //& a:after
        //{
        //  transform: rotate(180deg);
        //}
      }
      &.active {
        color: white;
      }
    }
    & li:last-child {
      margin-right: 0;
    }

  }
}
/* данное свойство необходимо для того, чтобы в браузерах
** Chrome и Safari можно было стилизовать инпуты */

input[type="search"] {
  -webkit-appearance: none;
}

@media screen and (min-width: 768px){

  input[type="search"]::-moz-placeholder {
    color: #768186; /* Цвет подсказывающего текста */
  }
  input[type="search"]::-webkit-input-placeholder { color: #768186; }
  input[type="search"]:-ms-input-placeholder { color: #768186; }
  input[type="search"]::-ms-input-placeholder { color: #768186; }
  input[type="search"]::placeholder { color: #768186; }
}
@media screen and (max-width: 768px){

  input[type="search"]::-moz-placeholder {
    color: #fff; /* Цвет подсказывающего текста */
  }
  input[type="search"]::-webkit-input-placeholder { color: #fff; }
  input[type="search"]:-ms-input-placeholder { color: #fff; }
  input[type="search"]::-ms-input-placeholder { color: #fff; }
  input[type="search"]::placeholder { color: #fff; }
}

