.footer {
  background: @black;

  & li {
    color: #fff;
    font-size: 13px;
    line-height: 17px;
    margin-bottom: 13px;
    font-weight: 300;
    & a {
      color: #fff;
      font-size: 13px;

    }
  }
  & p, & a {
    color: #fff;
    font-size: 13px;
    line-height: 17px;
    transition: 0.2s;
  }

  & p.footer-title {
    color: white;
    font-family: @title-font;
    font-size: 16px;
    text-transform: uppercase;
    margin-bottom: 30px;
    letter-spacing: 1.6px;
  }
  &-top
  {
    padding: 55px 0 49px;
  }
  &-btm {
    padding: 38px 0;
    border-top: 1px solid @pink;
    & p, & a {
      font-size: 12px;
      margin: 0;
    }
  }
  &-menu {
    margin-top: 16px;
  }
  &-adress {
    margin-bottom: 0;
  }
  &-link {
    display: block;
    &.phone {
      display: inline;
    }
    &.email {
      margin-top: 23px;
    }
  }
  &-more.last-btn {
    margin-top: 20px;
    display: block;
  }
  & a.footer-more.white {
    color: white;
    display: block;
    margin-top: 23px;
  }
}

& .copy {
  color: @pink;
  font-size: 22px;
  position: relative;
  top: 3px;
  margin-right: 9px;
}

.siluet {
  font-size: 12px;
  position: relative;
  padding-left: 12px;
  display: inline-block;
  float: right;
  padding-right: 2px;
  &:before {
    position: absolute;
    left: -21px;
    top: 0px;
    background: url(../img/siluet.png) no-repeat;
    width: 15px;
    content: "";
    height: 16px;
  }
}

