.price-filter {
  .selectric {
    background: #2b3434;
  }
}

.page-header {
  border-bottom: 1px solid black;
  margin-bottom: 45px;
  margin-top: 0;
  padding-bottom: 30px;
  & h1 {
    display: inline-block;
    margin-bottom: 0;
    margin-top: 0;
    letter-spacing: 1.5px;
  }
  & .price-filter {
    float: right;
  }
}

.main-block.special {
  padding: 0;
  & .special__item {
    margin-bottom: 30px;
  }
}

.filter-block {
  padding-right: 40px;
  & .filter {
    border-bottom: 1px solid lightgray;
    margin-bottom: 25px;
    &.active {
      & .filter-title {
        &:after {
          transform: rotate(180deg);
        }
      }
    }
    &-hide {
      display: none;
    }
    & .checkbox__text {
      margin: 20px 0;
      font-size: 12px;
      padding: 0 0 0 33px;
      color: black;
      .checkbox__text:before {
        width: 23px;
        height: 23px;
      }
    }
    & .checkbox:first-child .checkbox__text {
      margin-top: 0;
    }
    &.range-filter {
      & label {
        font-size: 11px;
      }
    }
    &__title {
      font-size: 20px;
      margin: 0;
      font-weight: 500;
      letter-spacing: 1.5px;
      text-transform: uppercase;
      font-family: @title-font;
      margin-bottom: 32px;
    }
    &-title {
      display: block;
      position: relative;
      font-weight: 900;
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 13px;
      text-transform: uppercase;
      &:after {
        position: absolute;
        content: url(../img/arr-b.png);
        right: 9px;
        top: -3px;
        transition: 0.3;
      }
    }

  }
}

.descr-accord-block {
  & .filter {
    margin-bottom: 11px;
    & p, & li {
      margin-top: 0;
      font-size: 14px;
      line-height: 25px;
    }
    &.active {

      & .filter-title {
        &:after {
          transform: rotate(180deg);
        }
      }
    }
    &-hide {
      display: none;
      padding-bottom: 10px;
    }
    &-title {
      display: block;
      position: relative;
      font-weight: 400;
      font-size: 12px;
      line-height: 20px;
      margin-bottom: 11px;
      text-transform: uppercase;
      border-bottom: 1px solid #b4bfc5;
      padding-bottom: 8px;
      &:after {
        position: absolute;
        content: url(../img/arr-b.png);
        right: 0;
        top: 0;
        transition: 0.3;
      }
    }

  }
}

.catalog {
  &-product {

    padding: 80px 0;
    &__gallery {
      &__img-container {
        line-height: 0;
        margin-bottom: 30px;
      }
    }
    &__text {
      margin-bottom: 95px;

      &__col {
        padding-top: 43px;
        height: 100%;
      }
      &-item {
        margin-bottom: 75px;
        &:last-child {
          margin-bottom: 0;
        }
        & h2 {
          font-size: 32px;
          text-transform: uppercase;
          margin: 0;
          margin-bottom: 35px;
        }
        & p {
          font-size: 16px;
          line-height: 25px;
          margin-bottom: 25px;
        }
      }
    }
    &__head {
      margin-bottom: 80px;
    }
    & .breadcrumbs {
      padding-top: 0;
      line-height: 10px;
      & ul {
        margin-top: 0;
        & li {
          line-height: 10px;
        }
      }
    }
    & .catalog-title {
      display: inline-block;
      position: relative;
      & h1 {
        display: inline-block;
        margin: 16px 0 4px;
      }
    }
    &__filter {
      border-top: 2px solid lightgray;
      position: relative;
      padding: 15px 0 17px;
      &-title {
        text-transform: uppercase;
        margin: 0;
        font-size: 12px;
        & span {
          text-transform: none;
        }
      }
      &:last-child {
        border-bottom: 1px solid lightgray;
      }
    }
    &__btns {
      padding: 25px 0;
    }
    &__description {
      margin: 21px 0;
      & .descr-accord-block {
        margin: 7px 0;
      }
    }
  }
}

.price {
  font-weight: 900;
  color: @pink;
  font-size: 25px;
  margin: 0;
  & span {
    font-weight: 700;
  }
}

.size-table__link {
  font-size: 12px;
  position: absolute;
  right: 0;
  top: 10px;
  text-decoration: underline;
}

.icon {
  background: @pink;
  width: 40px;
  height: 40px;
  line-height: 40px;
  color: white;
  font-size: 20px;
  position: relative;
  font-weight: 900;
  border-radius: 50%;
  display: inline-block;
  text-align: center;
  margin-right: 5px;
  vertical-align: top;
  &__block {
    position: absolute;
    right: -122px;
    top: 16px;

  }
  &:before {
    content: "";
    position: absolute;
    z-index: -45;
    bottom: 3px;
    left: 4px;
    display: block;
    width: 7px;
    height: 7px;
    transform: rotate(4deg);
    background: @pink;
  }
  &.new-icon {
    background: #a667ed;
    font-size: 16px;
    &:before {
      background: #a667ed;
    }
  }
}

.sol_slider {
  float: right;
  margin: 0;
  width: 80%;
  height: 580px;
  & img {
    height: 580px;
  }
}

.ctrl {
  position: relative;
  padding: 0 40px 17px;
  margin: 15px 30px 0;
  text-align: center;
  height: 580px;
  & .slick-slider,
  & .slick-list {
    height: 100%;
  }
  & .slick-slide {
    margin-bottom: 22px;
  }
  & .slick-prev,
  & .slick-next {
    transform: none;
    top: auto;
    margin-top: 0;
    width: 40px;
    height: 20px;
    border-radius: 0px;
    bottom: 0;
    left: 0;
    z-index: 5;
    right: 0;
    margin: auto;
    background: url(../img/arr_bot.png) no-repeat center;
    &:before {
      content: none;
    }
  }
  & .slick-prev {
    background: url(../img/arr_top.png) no-repeat center;
    left: 0;
    top: 0;
    bottom: auto;
  }
}

.ctrl {
  padding: 0;
  margin: 0;
  float: left;
  text-align: center;
  overflow: hidden;
  width: 17%;
}

.catalog {
  &-item {
    & h1 {
      margin-top: 0;
      margin-bottom: 12px;
    }
    &__text {
      padding-left: 63px;
      & .btn {
        margin-bottom: 20px;
        margin-top: 12px;
      }
    }
    &__tab {
      &-list {
        & li {
          display: inline-block;
          position: relative;
          cursor: pointer;

          & .descr {
            display: none;
            position: absolute;
            top: -50px;
            left: -20px;
            font-size: 15px;
            background: white;
            padding: 5px 15px;
            box-shadow: 0 0 5px grey;
            border-radius: 5px;
            transition: 0.3s;
            &:before {
              width: 8px;
              height: 8px;
              content: "";
              position: absolute;
              bottom: -5px;
              left: 36px;
              //right: 0;
              margin: auto;
              display: block;
              z-index: 45;
              border-right: 1px solid lightgray;
              border-bottom: 1px solid lightgray;
              transform: rotate(45deg);
              background: #fff;
            }
          }
          &:hover .descr {
            display: block;
          }
        }
      }
      &-text {
        padding-top: 31px;
      }
      &-content {
        & h3 {
          margin-bottom: 17px;
        }
        & p {
          margin-bottom: 30px;
          font-size: 17px;
          line-height: 30px;
        }
      }
    }
    &__descr {
      margin-top: 12px;
      font-size: 17px;
      line-height: 20px;
    }
    &__price {
      font-size: 25px;
      line-height: 25px;
      color: #8d9fa7;
      margin-bottom: 0;
    }
    &__sale {
      font-size: 15px;
      line-height: 25px;
      margin-top: 4px;
      margin-bottom: 20px;
      & span {
        font-weight: bold;
        color: @blue;
      }
      &__info {
        font-size: 15px;
        line-height: 25px;
      }

    }
    &__tabs {
      width: 100%;
      border-bottom: 2px solid #f0f0f0;
      margin: 0;
      margin-top: 5px;
      & li {
        text-transform: uppercase;
        margin: 0;
        background: #f0f0f0;
        display: inline-block;
        border-top-right-radius: 6px;
        border-top-left-radius: 6px;
        font-size: 13px;
        color: #505050;
        margin-right: 1px;
        cursor: pointer;
        padding: 7px 24px;
        transition: 0.3s;
        &:hover {
          //opacity: 0.7;
          color: #fff;
          background: @blue;
        }
        &.active {
          color: #fff;
          background: @blue;
        }
      }
    }
  }
  &-list {
    & h1 {
      margin-bottom: 40px;
    }
  }

}
