.btn {
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 10px;
  font-weight: 700;
  border-radius: 50px;
  background: transparent;
  padding: 10px 23px 10px;
  line-height: 1.2em;
  vertical-align: middle;
  color: #2b3434;
  text-transform: uppercase;
  border:2px solid @pink;
  transition: 0.4s;
  &:hover
  {

    box-shadow: 0 0 10px 2px rgba(0,0,0,0.4);
    background: @pink;
    color: white;
    opacity: 0.7;
  }
  &.sml-btn
  {
    font-size: 15px;
    font-weight:400;
    padding: 7px 14px 7px;
    margin-right: 3px;
  }
}

.more-btn
{
  font-size: 15px;
  font-weight:400;
  padding: 7px 14px 7px;
  background: #20467b;

}
.cart-btn
{
  color: #2b3434;
  border-color: #2b3434;
  font-size: 12px;
  padding: 10px 35px 10px 18px;
  margin-right: 8px;
  position: relative;
  &:after
  {
    content: url("../img/arr-r-r.png");
    position: absolute;
    right: 14px;
    top:10px;
  }
  &:hover
  {
    background: #000;
    &:after
    {
      filter: invert(1);
    }
  }
  //margin: 0 0 36px;
}
.add-cart__btn
{
  font-size: 12px;
  background: #2b3434;
  color: white;
  text-transform: uppercase;
  font-weight: 900;
  border: none;
  vertical-align: top;
  position: relative;
  margin-left: 9px;
  padding: 11px 14px 11px;
  padding-left: 42px;
  &:before
  {
    content: url(../img/bag-icon.png);
    position: absolute;
    left: 14px;
    top: 6px;
  }
}
.header-btn {
  display: inline-block;
  width: 130px;
  padding: 10px;
  position: absolute;
  right: -23px;
  top: 10px;
}

.nav-btn
{
  display: inline-block;
  box-sizing: border-box;
  text-align: center;
  text-decoration: none;
  cursor: pointer;
  font-size: 14px;
  font-weight: 400;
  border-radius: 50px;
  background: #f4f5f6;
  padding: 3px 23px 3px;
  line-height: 25px;
  vertical-align: middle;
  background-repeat: no-repeat;
  color: black;
  &.next {
    background-image: url("../img/arr-r-sl.png");
    background-position: 87%;
    padding-right: 42px;

  }
  &.prev {
    background-image: url("../img/arr-l-sl.png");
    background-position: 13%;
    padding-left: 38px;

  }
}


.slider-btn {
  background: @blue;
  color: #fff;
  padding: 8px 17px;
  font-size: 12px;
  font-weight: 700;
  margin-top: 35px;
  margin-bottom: 44px;
  &:hover
  {
    box-shadow: none;
    transform: none;
  }
}
.footer-btn {
  font-size: 15px;
  font-weight: 400;
  padding: 4px 13px 5px;
  &:hover
  {
    color: white !important;
  }
  //background-color: #212933;
  //color: white;
