  //padding: 8px 20px;
  //font-size: 12px;
  //margin-right: 5px;
  //border: 1px solid #656b72;
  //display: inline-block;
}

.blog-btn {
  border: 2px solid #2b3434;
  font-size: 12px;
  padding: 10px 40px 10px 16px;
  border-color: #2b3434;
  border-radius: 50px;
  font-weight: 900;
  text-transform: uppercase;
  background-image: url('../img/arr-l.png');
  background-repeat: no-repeat;
  background-position: 80px 14px;
}

.blog-btn:hover {
  background-color: @pink;
  color: #fff;
  border: 2px solid #fff;
  background-image: url('../img/arr-r-w.png');
}
