@media screen and (max-width: 1199px) {
  .navbar {
    & .header-menu {
      margin-right: 52px;
      margin-left: 0;
      & li {
        margin-right: 30px;
      }
    }
    & .soc-icons {
      margin-left: 12px;
    }
    .header-phone {
      margin-left: 0;
    }
  }

  .jumbotron {
    &__slider {
      & .slick-dots {
        width: auto;
        top: -45px;
      }

    }
  }

  .categories {

    & .category {
      overflow: hidden;
      &__text {
        width: auto;
      }
    }
    & .category.left {
      & .category__text {
        padding-right: 50px;;
        padding-left: 50px;
      }
    }
    & .category__text {
      padding-right: 50px;;
      padding-left: 50px;
    }
  }

}

@media screen and (max-width: 991px) {
  .navbar {
    & .header-menu {
      margin: 0;
      margin-right: 95px;
      & li {
        font-size: 12px;
        margin: 0;
        margin-right: 19px;
        padding: 0;
        &.drop-menu {
          & > a:after {
            right: -12px;
            top: 5px;
          }
        }
      }
    }
    & .search-col input[type=search] {
      width: 85%;
    }
    & .search-col .search .submit {
      right: -3px;
    }
    & .soc-icons, & .header-phone {
      display: none;
    }
  }

  .instagram__link {
    margin-bottom: 20px;
    & img {
      width: 100%;
    }
  }

  .left-menu {
    &__tabs {
      margin: 0;
    }

    &__tabs li {
      margin: 0;
      display: block;
    }
  }

  .ctrl,
  .sol_slider {
    width: 100%;
    height: auto;
  }
  .sol_slider img {
    height: auto;
    width:100%;
  }
  .sol_slider
  {
    padding-right: 3px;
  }
  .ctrl
  {
    margin-top: 5px;
  }
  .ctrl .slick-slide {
    margin-bottom: 0;
    margin-right: 5px;

  }
  .ctrl .slick-prev {
    left: -10px;
    top: 0;
    bottom: 0;
    transform: rotate(-90deg);
    right: auto;
  }
    .ctrl .slick-next {
      right: -7px;
      top: 0;
      bottom: 0;
      transform: rotate(-90deg);
      left: auto;
    }

  .breadcrumbs {
    padding-top: 5px;
    margin-bottom: 20px;
  }

  .service-form, .service-form.form.contact {
    margin-top: 20px;
    padding: 4px 20px;
    & .btn-form {
      top: 0;
      display: block;
      margin: 26px 0 15px;
    }
  }

  .popular__item__btns {
    display: block;
  }

  .catalog-category {
    padding: 0;
    line-height: 0;
    & .more-link {
      top: auto;
    }
    & img {
      position: relative;
    }
    &__text {
      padding: 15px;
      & h3 {
        margin-top: 0;
      }
    }
  }
  .special {
    &.bottom
    {
      padding-bottom: 0;
    }
    &__item {
      text-align: center;
      margin-bottom: 20px;
      & a {
        display: inline-block;
        position: relative;
      }
    }
  }
  .catalog-item {
    &__tab {
      &-text {
        padding-top: 0;
      }
    }
    &__tabs {
      margin-top: 30px;
      & li {
        padding: 7px 10px;
        font-size: 12px;
      }
    }
    &__text {
      padding-left: 0;
      & .btn {
        width: 100%;
        font-size: 15px;
        padding: 10px;
      }
    }
  }

  .banners .progress-div {
    position: relative;
    top: -3px;
    line-height: 0;
  }
}

@media screen and (max-width: 769px) and (min-width: 768px) {
  .left-menu-col,
  .main-col {
    width: 100%;
  }
}

@media screen and (max-width: 768px) {
  .modal-content .modal-header .close
  {
    top: 19px;
    right: 12px;
  }
  .modal-content .add-info
  {
    width:100%;
  }
.pinned
{
  & td
  {
    padding-left: 5px !important;
  }
}
  .jumbotron {
    & .logo-main {
      z-index: 10;
      position: absolute;
      top: 0;
      bottom: 0;
      left: 0;
      margin: auto;
      right: 0;
      max-width: 80%;
    }
    &__slider {

      & .slide img {
        width: 200%;
        max-width: initial;
        margin-left: -60%;
      }
    }

  }

  .cart-icon {
    position: absolute;
    right: 42px;
    top: 4px;
  }


  .catalog
  {
    &-product
    {
      padding: 30px 0;
      &__head
      {
        margin-bottom: 0;
      }
      &__text-item {
        margin-bottom: 25px;
      }
    }
  }
  .categories {
    & .category {
      margin-bottom: 30px;
      &.left .category__text {
        padding: 20px 0;
      }
      &:last-child {
        margin-bottom: 0;
      }
      &__text {
        position: inherit;
        padding: 20px 0;
      }
    }
  }

.mob-top
{
  margin-bottom: 30px;
}

  .filter {
    &-block {
      padding-right: 0;
    }
    & .checkbox {
      display: inline-block;
      margin-right: 25px;
    }

  }

  p,
  .text p,
  .text li,
  li {
    font-size: 15px;
    line-height: 28px;
    margin: 16px 0;
    // }
  }

  .text ul li {
    margin-bottom: 10px;
    margin-top: 10px;
  }

  .modal {
    &-content {
      padding: 5px;
      & .modal-title {
        line-height: 26px;
        font-size: 20px;
      }
      & .btn-form {
        margin-top: 30px;
        width: 100%;
      }
    }
  }

  .content {
    padding-bottom: 20px;
  }

  .main-block {
    & h1 {
      margin-bottom: 10px;
    }
  }

  .navbar {
    padding: 0;
    box-shadow: none;
    margin-bottom: 0;
    & .mobile-close {
      display: block;
    }
    & .logo img {
      max-width: 70%;
    }

    &.fixed {
      z-index: 1000;
    }
    & .mob-title {
      display: block;
    }
    & .search-col {
      & .search-form {
        width: 80%;
        left: 0;
        padding-left: 0;
        float: left;

      }
      & input[type=search] {
        width: 89%;
        float: left;
        background: transparent;
      }
      & .search .submit {
        right: -10px;
        background: url(../img/search-w.png) 50% no-repeat;
      }
    }
    & .header-menu {
      display: block;
      margin-right: 0;

      & li {
        display: block;
        text-align: left;
        color: @pink;
        font-size: 14px;
        text-transform: none;
        padding: 8px 0;
        margin-right: 0;
        border-bottom: 1px solid rgba(255, 255, 255, 0.19);
        &:hover {
          color: white;
        }
        &:last-child {
          border-bottom: none;
        }
        &.active {
          color: white;
        }
        &.drop-menu > a {
          display: block;
          &:after {
            background: url(../img/arr-r-w.png);
            width: 5px;
            height: 8px;
            right: 0;
          }
        }

      }

    }
    & .mobile-menu {

      width: 100%;
      border-bottom: 1px solid @pink;
      margin: 19px 0 17px;
      padding: 12px 0 7px;
    }
    & .btn_mob.open {
      z-index: 1000;
      & span {
        background: #fff;
      }
    }

    & .menu-block {
      // display: none;
      overflow: scroll;
      text-align: left;
      margin: 0;
      position: fixed;
      top: 0;
      right: -300px;
      background: @black;
      padding: 0 26px 0 20px;
      height: 100%;
      width: 300px;
    }
  }

  .header-btn {
    display: inline-block;
    width: 101px;
    padding: 6px;
    position: absolute;
    right: 72px;
    top: 7px;
    font-size: 14px;
  }

  .mobile-toggle {
    display: block;
  }

  .header-contacts {
    & .header-phone, & .header-email {
      float: inherit;
      color: white;
      display: block;
      margin-left: 0;
      margin-top: 0;
    }
  }

  .left-menu-block.fixed {
    z-index: 112;
  }

  .left-menu {
    display: block;
    margin: 0;
    //overflow-y: scroll;
    position: fixed;
    top: 0;
    left: -270px;
    z-index: 22;
    background: white;
    padding: 15px 25px 0 20px;
    height: 100%;
    width: 270px;
    &__list {
      overflow: scroll;
      //height: 100%;
      margin-top: 5px;
      padding-top: 6px;
      border-top: 1px solid #e5e5e5;
      height: 90%;

    }
    &__title {
      display: block;
    }
    & .logo {
      width: 43%;
      display: inline-block;
    }
    & .header-btn {
      display: inline-block;
      vertical-align: top;
      position: initial;
      margin-left: 22px;
    }
    &-block {
      //margin-bottom: 20px;
      & .sep {
        display: block;
        position: absolute;
        right: 0;
        left: 0;
        margin: auto;
        top: 5px;
        height: 25px;
        border-right: 1px solid rgba(0, 0, 0, 0.1);
        content: '';
        width: 1px;
      }
    }
    &__tabs {
      white-space: nowrap;
      border-bottom: none;
      & li {
        padding: 1px 13px;
        font-size: 13px;
        margin: 0;
        font-weight: 400;
        color: #1e2528;
        display: inline-block;
        width: 45%;
        background: #e5e5e5;
        border-radius: 5px;
        position: relative;
        &:first-child {
          margin-right: 9%;
        }
        &.active {
          border-bottom: none;
        }
        &:before {
          position: absolute;
          right: 12px;
          top: 12px;
          background: url(../img/arr-bb-sl.png);
          background-size: cover;
          width: 10px;
          height: 7px;
          content: "";

        }
      }
    }
  }

  .siluet {

    float: left;
    padding-left: 30px;
    margin-top: 5px !important;
    display: block;
    &:before {
      left: 0;
    }
  }

  .service-form {
    & .btn-form {

      width: 100%;
      margin: 16px 0;
    }
  }

  .footer {
    &-top {
      padding: 10px 0 20px;
    }
    & p.footer-title {
      margin-top: 35px;
    }
  }
}

@media screen and (max-width: 560px) {
  .cart {

    padding: 10px;
    width: 93%;
    &-btn {
      width: 100%;
      margin-bottom: 5px;
    }
    &__item table td:nth-child(2) {
      width: 150px;
    }
    &__close {
      right: 10px;
      top: 26px;
    }
    & ul.summary {
      & li:first-child {
        width: auto;
      }
      & li:nth-child(2) {
        float: right;
      }
    }
  }

  .page-header {
    padding-bottom: 10px;
    margin-bottom: 20px;
    & h1
    {
      margin-bottom: 15px;
    }
    & .price-filter {
      float: inherit;
      height: 40px;
    }
  }
}

@media screen and (max-width: 460px) {
  .pagination .pages {
    & li {
      & a {
        padding: 3px;
      }
      &:last-child {
        & a {
          padding-left: 20px;

        }
        &:after {
          left: 0;
        }
      }
      &:first-child {
        & a {
          padding-right: 20px;

        }
        &:after {
          right: 0;
        }
      }
    }
  }
}

@media screen and (max-width: 425px) {

  .special__tabs {
    width: 100%;
    margin-left: 0;
    & li {
      width: 48%;
      margin-right: 0;
      margin: 0;
    }
  }

  .text img {
    margin-bottom: 5px;
  }

  .more-link {
    position: relative;
    right: 0;
    top: -22px;
  }

}
