
.blog {
  h3 {
    margin-bottom: 0;
    font-size: 20px;
    line-height: 25px;
    text-transform: uppercase;
    max-width: 90%;
    margin-top: 6px;
    font-weight: 500;
  }
  
  p {
    display: inline-block;
    font-size: 14px;
    line-height: 20px;
  }
  
  .data {
     font-style: @text-font;
    font-weight: 900;
    font-size: 12px;
    color: @pink;
  }
  
  &-mainimage {
    margin-bottom: 64px;
  }
  
  &-main_article {
    padding-left: 50px;
    padding-top: 30px;
    padding-right: 40px;
  }
  
  &-main_article p {
    font-size: 17px;
    line-height: 25px;
    margin-bottom: 40px;
  }
  
   &-main_article h3 {
    font-size: 25px;
    line-height: 40px; 
    text-transform: uppercase;
    
 
  }
  
   &-main_article .data {
    font-style: @text-font;
    font-weight: 900;
    font-size: 18px;
    color: @pink;
  }
  
  &-item {
    margin-bottom: 40px;
    min-height: 420px;
    position: relative;
  }
  
  &-item:hover::after {
    position: absolute;
    z-index: -1;
    top: -20px;
    left: -18px;
    display: block;
    width: 110%;
    height: 110%;
    content: '';
    background-color: #fff;
    box-shadow: 0 5px 10px 0 rgba(0, 0, 0, .1);
   }
  
  &-modal-dialog.modal-dialog {
    width: 100%;
  }

  &-model-content {
    
    h3 {
      font-size: 25px;
      line-height: 40px;
      text-transform: uppercase;
      padding-right: 35px;
    }
    
    p {
      font-size: 16px;
      line-height: 25px;
      color: #2b3434;
      display: inline-block;
      margin-bottom: 30px;
    }
    
    .data {
      font-style: @text-font;
      font-weight: 900;
      font-size: 18px;
      color: @pink;
   }
  
    
    img {
      margin-bottom: 60px;
    }
    
    .btn {
      font-weight: 900;
      margin-top: 54px;
      border: 2px solid #2b3434;
      padding: 10px 50px 10px 15px;
      background-image: url("../img/btn-bl-close.png");
      background-repeat: no-repeat;
      background-position: 72px 11px;
      background-size: 10px 10px;
    }
    
    .btn:hover {
      border: 2px solid #fff;
       background-image: url("../img/btn-bl-close2.png")
    } 
  }
 }

.blog.pagination {
 
  border-top: 1px solid #000;
}




@media screen and (max-width: 992px) {
  .blog {
    &-mainimage {
      margin-top: 40px;
      margin-bottom: 0;
    }
    
     &-main_article {
       padding-left: 0;
       padding-top: 30px;
       padding-right: 40px;
       margin-bottom: 100px;
    } 

      
 
     &-item:hover {
      outline: none;
      background-color: transparent;
      box-shadow: none; 
   }
    
    &-item:hover::after {
      display: none;
    }

  
    p {
      margin-bottom: 30px;
    }
    
    .data {
      line-height: 40px;
    }
    
  }
}