.new {
  width: 100%;
  background-color: #fbfbfb;
  //min-height: 420px;
  margin-bottom: 50px;
  position: relative;
  padding-bottom: 15px;

  &img {
    margin-bottom: 20px;
  }

  &-title {
    padding: 0;
    margin: 0;
    color: @blue;
    font-size: 17px;
    line-height: 20px;
    font-weight: bold;
    margin-bottom: 6px;
    padding-left: 30px;
    padding-right: 30px;
    margin-top: 25px;
  }

  &-text {
    font-size: 14px;
    line-height: 20px;
    margin: 0;
    padding: 0;
    //font-weight: bold;
    display: inline-block;
    padding-left: 30px;
    padding-right: 30px;
    margin-bottom: 20px;
  }

  &-data {
    position: absolute;
    bottom: 20px;
    right: 29px;
    font-size: 10px;
    font-weight: bold;
    text-align: right;

  }
}

.news-one {
  & h1 {
    margin-bottom: 20px;

  }
  &__data {
    margin-top: 0;
    font-weight: bold;
    font-size: 10px;
    margin-bottom: 28px;
  }
  &__btn
  {
    font-weight: 400;
    padding: 7px 10px;
  }
  &__img {
    margin-bottom: 54px;
    & img {
      width: 100%;
    }
  }
}

.news-links
{
  margin-top: 100px;
  padding: 35px 0 33px;
}