.contacts {
  &-title {
    font-family: @title-font;
    font-size: 20px;
    font-weight: 500;
    padding-bottom: 30px;
    width: 100%;
    display: inline-block;
    border-bottom: 1px solid #dbd2d8;
  }
  
  &-title + p {
    padding-top: 30px;
  }
  
  &-map {
    margin-bottom: 70px;
    min-height: 512px;
    width: 100%; 
  }

  &-form {
    
    label {
      padding-top: 10px;
      width: 100%;
      margin-top: 12px;
      display: block;
    }
    
    input {
      width: 100%;
    }
    
    textarea {
      width: 100%;
      min-height: 120px;
    }
    
    input[type="checkbox"] {
      opacity: 0;
    }
    
    label[for="name"] {
      margin-top: 26px;
    }
    
     input[id="name"] {
      margin-top: 26px;
    }
    
    label[for="order"] {
//      color: red;
      padding-left: 34px;  
      position: relative;
    }
    
    label[for="order"]::before  {
      content: "";
      display: block;
      background-color: #fff;
      width: 22px;
      height: 22px;
      border-radius: 4px;
      position: absolute;
      left: 0;
    }
    
    
    &-block {
      margin-left: 35px;
      padding-left: 60px;
      border-left: 1px solid #dbd2d8;  
    }
    
    sup {
      color: @pink;
    }
    
    .checkbox__text {
      color: @text-color;
      padding-left: 34px;
    }
    
    .checkbox__text a {
      text-decoration: underline;
    }
    
  }
}

#phone {
  width: 140px;
}

.btn.form {
  width: 140px;
  height: 35px;
  border: 2px solid #fff;
  font-weight: 900;
  background-image: url('../img/ema.png');
  background-repeat: no-repeat;
  background-size: 21px 17px;
  background-position: 16px 8px;
  padding-left: 42px;
  margin-top: 30px;
  margin-bottom: 70px;
}

.btn.form:hover {
   background-image: url('../img/ema2.png');
}

#map {
  width: 100%;
  min-height: 512px;
}

@media screen and (max-width: 992px) {
  .contacts-form-block {
    margin-left: 0;
    padding-left: 0;
  }
  
  .contacts-title {
    margin-top: 50px;
  }
  
  .page-header {
    margin-bottom: 0;
  }
}