.shoppingCart {
  table {
    width: 100%;
    border: 0;
    text-align: center;
  }
  
  tr {
    border-color: #d5d4db;
    text-align: center;
  }
  
  td:first-child {
    text-align: left;
  }
  
  td {
    text-align: center;
    vertical-align: middle;
  }
  
  .total {
    text-align: right;
    line-height: 60px;
  }
  
  .save {
    text-align: right;
  }
  
  img {
    float: left;
  }

  .table-header {
    color: #2b3434;
    font-style: @base-font;
    font-size: 12px;
    text-transform: uppercase;
    font-weight: 900;
    border-top: none;
    padding-bottom: 40px;
  }
  
  .table-header td {
    padding-bottom: 40px;
  }
  
  .table-bottom {
    border-bottom: 1px dashed #cabbc2;
  }
  
  .table-bottom td {
    padding-top: 30px;
    padding-bottom: 30px;
    border-top: 1px solid #000;
  }

  .count-left {
    font-weight: 900;
    width: 36px;
    height: 36px;
    background-color: #fff;
    border-bottom-left-radius: 50px;
    border-top-left-radius: 50px;
    line-height: 36px;
    display: inline-block;
    position: relative;
  }
  
  .count-left::after {
    content: "";
    display: block;
    width: 4px;
    height: 36px;
    background-color: #fff;
    position: absolute;
    right: -4px;
    top: 0px;
  }
  
  
  
  .count-center {
    margin: 0;
    font-weight: 900;
    background-color: #fff;
    height: 36px;
    width: 48px;
    line-height: 36px;
    display: inline-block;
  }
  
   .count-right {
    width: 36px;
    height: 36px;
    background-color: #fff;
    border-bottom-right-radius: 50px;
    border-top-right-radius: 50px;
    line-height: 36px;
    display: inline-block;
    font-weight: 900;
    position: relative 
  }
  
    .count-right::after {
    content: "";
    display: block;
    width: 4px;
    height: 36px;
    background-color: #fff;
    position: absolute;
    left: -4px;
    top: 0px;
  }
  
//  .table-header td {
//    margin-bottom: 40px;
//  }
  
  .size {
    font-size: 13px;
  }
  
  .size p {
    line-height: normal;
  }
  
  .size span {
    margin: 0;
    padding: 0;
    font-size: 13px;
    display: inline-block;
    margin-left: 18px;
    line-height: normal;
  }
  

  .size .art {
    display: inline-block;
    padding-top: 4px;
    font-size: 14px;
    text-transform: uppercase;
    text-decoration: underline;
    margin-bottom: 6px;    
  }
  
  .btn {
    border: 2px solid #fff;
    font-size: 12px;
    font-style: @base-font;
    font-weight: 900;
  }
  
  .btn.checkout {
    padding-left: 44px;
    border: 2px solid #0e1111;
    color: #fff;
    background-color: #0e1111;
    background-image: url("../img/checkout.png");
    background-repeat: no-repeat;
    background-position: 13px 7px;
    background-size: 18px auto;
  }
  
  .btn.checkout:hover {
    background-color: @pink;
    border: 2px solid #fff;
  } 
}