.ui-slider-horizontal {
  height: 0.14em;
}

.ui-state-default, .ui-widget-content .ui-state-default {
  border-radius: 50%;
  border: 2px solid black;
  background: @pink;
  width: 0.7em;
  height: 0.7em;
  cursor: pointer;
}

.ui-widget-header {
  background: #1a1f1f;
}

.ui-widget-content {
  background: #90a0a0;
}

.price-input {
  background: transparent;
  font-size: 11px;
  color: #2b3434;
}

.selectric-wrapper {
  position: relative;
  display: inline-block;
  cursor: pointer;
  width: 100%;
  float: right;
  text-align: left;
  margin-bottom: 13px;
}

.selectric {
  background: #2b3434;
  border-radius: 50px;
  padding: 0px 25px 0 10px;
  & .label {
    color: white;
    font-weight: bold;
    font-size: 14px;
  }
  & .button {
    background: url(../img/arr-b-slw.png) no-repeat center;
    top: 0;
    &:after {
      content: none;
    }

  }
}

.incr-input {
  border-radius: 50px;
  text-align: center;
  padding: 8px 23px;
  width: 120px;
  font-size: 16px;
  font-weight: bold;
  margin: 0;
  &__div {
    position: relative;
    display: inline-block;
    & .input-button
    {
      position: absolute;
      right: 0;
      top: 0;
      text-align: center;
      width: 36px;
      height: 35px;
      border-radius: 50px;
      font-size: 16px;
      padding-top: 6px;
      cursor: pointer;
      &.dec
      {
        right: auto;
        left:0;
      }
    }
  }

}


.checkbox {
  position: relative;
}

.checkbox input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 20px;
}

.checkbox__text {
  position: relative;
  padding: 0 0 0 60px;
  cursor: pointer;
  font-size: 14px;
  color: #979da4;
}

.checkbox__text:before {
  content: '';
  position: absolute;
  top: -2px;
  bottom: 0;
  margin: auto;
  left: 0;
  width: 20px;
  height: 20px;
  background: #fff;
  border-radius: 3px;
  border: 1px solid @input-border;
  transition: .2s;
}

.checkbox input:checked + .checkbox__text {
  color: @navy;
}

.checkbox input:checked + .checkbox__text:before {
  background-image: url(../img/tick.png);
  background-repeat: no-repeat;
  background-position: center;
}

//.checkbox input:checked + .checkbox__text:before {
//  background: #9FD468;
//}
//.checkbox input:checked + .checkbox__text:after {
//  left: 26px;
//}
.checkbox input:hover + .checkbox__text:before {
  box-shadow: inset 0 2px 3px rgba(0, 0, 0, .2);
}

.radio input {
  position: absolute;
  z-index: -1;
  opacity: 0;
  margin: 10px 0 0 7px;
}

.radio__text {
  position: relative;
  padding: 0 0 0 47px;
  cursor: pointer;
  margin-bottom: 16px;
}

.radio__text:before {
  content: '';
  position: absolute;
  top: 0;
  left: 6px;
  width: 20px;
  height: 20px;
  border: 1px solid #CDD1DA;
  border-radius: 50%;
  background: #FFF;
}

.radio__text:after {
  content: '';
  position: absolute;
  top: 7px;
  left: 13px;
  width: 6px;
  height: 6px;
  border-radius: 50%;
  background: #232b35;
  opacity: 0;
  transition: .2s;
}

.radio input:checked + .radio__text:after {
  opacity: 1;
}


.choose-btns
{
  line-height: 19px;
  & .radio
  {
    display: inline-block;

    margin-right: 26px;
    & input:checked + .radio__text{
      font-weight: 900;
    }
    & .radio__text
    {
      font-size: 12px;
      font-weight: 200;
      padding: 0;
      margin: 0;
    }
    & .radio__text:before,
    & .radio__text:after
    {
      content: none;
    }
  }
  &.colors
  {
    & .radio
    {
      margin-right: 17px;
    }
    & .radio__text
    {
      width: 12px;
      height: 12px;
      border-radius: 50%;
    }
    & input:checked + .radio__text:before{
      content: '';
      border: 1px solid black;
      width: 20px;
      height: 20px;
      background: none;
      position: absolute;
      top: 0;
      bottom: 0;
      margin: auto;
      left: -4px;
      display: block;
    }
  }
}
button[disabled], html input[disabled] {
  background: white;
}

