/* Always set the map height explicitly to define the size of the div
 * element that contains the map. */

/* Optional: Makes the sample page fill the window. */
html, body {
  height: 100%;
  margin: 0;
  padding: 0;
  min-width: 320px;
}

.content {
  padding-bottom: 70px;
  &.main-content {
    padding-bottom: 0;
  }
}

body.overlayed {
  transition: 0.5s;
  overflow: hidden;
  &:before {
    position: fixed;
    z-index: 20;
    top: 0;
    left: 0;
    display: block;
    width: 100%;
    height: 100%;
    content: '';
    background: rgba(0, 0, 0, 0.4);
  }
}

a.fancybox-item.fancybox-close {
  background: url(../img/moda-cross.png);
  background-size: contain;
}

.vcenter {
  display: inline-block;
  vertical-align: middle;
  float: none;
}

* {
  outline: none;
}

.list {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;

  -webkit-flex-wrap: wrap;
  -ms-flex-wrap: wrap;
  flex-wrap: wrap;
}

.list__item {
  display: -webkit-flex;
  display: -ms-flexbox;
  display: flex;
}

.pl0 {
  padding-left: 0;
}

ul {
  padding-left: 0;
}

ul li {
  list-style-type: none;
}

.rotatable {
  -webkit-transform: rotate(-90deg);
  -moz-transform: rotate(-90deg);
  -o-transform: rotate(-90deg);
  -ms-transform: rotate(-90deg);
  transform: rotate(-90deg);
}

