    }

  }
  & th {
    font-size: 15px;
    line-height: 20px;
    font-weight: 700;
    text-align: left;
    padding: 10px;
    background: lightgray;
  }
  & td {
    font-size: 15px;
    padding: 15px;
    padding-left: 0;
    line-height: 20px;
    font-weight: 400;
    text-align: left;
    vertical-align: text-bottom;
  }
}

.text {
  //padding: 30px 100px;
  & p {
    font-size: 17px;
    line-height: 30px;
    margin-bottom: 30px;
  }
  & img {
    margin: 15px 0 25px;
  }
  & h2 {
    margin-top: 38px;
    margin-bottom: 30px;
    color: #8d9fa7;
  }
  & h3 {
    margin: 40px 0 28px;
    font-size: 28px;
  }
  & h4 {
    margin: 37px 0 20px;
    font-size: 26px;
  }
  & h4 {
    margin: 36px 0 20px;
  }
  & ul li {
    position: relative;
    padding-left: 42px;
    margin-bottom: 14px;
    line-height: 26px;
    &:before {
      content: '•';
      color: @blue;
      left: 0;
      font-size: 20px;
      position: absolute;
    }
  }
  ol {
    counter-reset: item;
    padding-left: 0;
  }
  & ol li {
    position: relative;
    list-style-type: none;
    padding-left: 31px;
    margin-bottom: 10px;
    line-height: 30px;
    &:before {
      content: counter(item) '.';
      counter-increment: item;
      left: 0;
      font-size: 17px;
      position: absolute;
    }
  }
}

