.progress {
  border: 0;
  width: 100%;
  height: 5px;
  border-radius: 10px;
  background: #f1f1f1;
}

.progress::-webkit-progress-bar {
  width: 100%;
  height: 5px;
  border-radius: 10px;
  background: #f1f1f1;
}

.progress::-webkit-progress-value {
  border-radius: 10px;
  background: @blue;
}

.progress::-moz-progress-bar {
  border-radius: 5px;
  background: @blue;
}

.cart {
  z-index: 20;
  position: fixed;
  top: 0;
  right: -500px;
  background: white;
  height: 100%;
  padding: 20px 60px;
  width: 500px;
  & ul.summary {
    padding: 5px 0;
    margin: 0;
    border: none;
    & li {
      border: none;
      display: inline-block;
      font-size: 14px;
      & span {
        font-weight: bold;
      }
      &:hover {
        background: #fff;
      }
      &:first-child {
        width: 300px;
      }
    }
  }
  &__list {
    padding-left: 0;
    margin-top: 19px;
    border-top: 1px solid black;
    border-bottom: 1px solid black;
    margin-bottom: 0;
  }
  &__item {
    padding: 0;
    & table {
      width: 100%;
      & tr {
        border-top: 0;
        border-bottom: 0;
      }
      & td {
        vertical-align: middle;
        padding: 0;
        &:first-child {
          width: 60px;
        }
        &:nth-child(2) {
          padding: 0 5px 0 20px;
          width: 240px;
          & p {
            text-transform: uppercase;
          }
        }
      }
      & p {
        margin: 0;
        font-size: 14px;

      }
    }

  }
  &__btns {
    margin-top: 37px;
  }
  &__title {

    font-size: 20px;
    color: #2b3434;
    font-family: @title-font;
    text-transform: uppercase;
    letter-spacing: 1px;
    display: inline-block;
    font-weight: 700;
  }
  &__close {
    position: absolute;
    right: 67px;
    top: 36px;
  }

  & li {
    color: #768186;
    font-size: 15px;
    line-height: 20px;
    position: relative;
    padding: 19px 0 17px;
    border-bottom: 1px solid lightgray;
    margin: 0;
    transition: 0.3s;
    &.active {
      color: #000;
    }
    &:hover {
      background: #f1f1f1;
    }
    &:first-child {
      margin-top: 0;
    }
  }

}

