.jumbotron {
  position: relative;
  margin-bottom: -59px;

  & .logo-main {
    z-index: 10;
    position: absolute;
    top: 0;
    bottom: 0;
    margin: auto;
  }
  &__slider {
    & .slick-dots {
      margin-right: auto;
      margin-left: auto;
      padding-left: 15px;
      padding-right: 15px;
      width: 1200px;
      text-align: right;
      position: relative;
      top: -70px;
      & li
      {
        margin: 0 2px;
      }
      & li button:before {
        color: @pink;
        opacity: 1;
      }
      & li.slick-active button {
        &:before {
          content: '';
          opacity: 1;
          border: 2px solid @pink;
          border-radius: 50%;
          width: 10px;
          height: 10px;
          top: 5px;
          left: 4px;
        }
      }
    }

    & .slide {
      & img {
        width: 100%;
      }
    }
  }
}

.parent {display: table;}
.child {
  display: table-cell;
  vertical-align: middle;
}
.categories
{
  & .category
  {
    padding:0  15px ;
    &__text
    {
      width: 590px;
      position: absolute;
      right: 0;
      height: 100%;
      padding-right: 200px;
      & .btn
      {
        margin-top: 20px;
      }
      & .info{
        margin:0;
        font-size: 14px;
        line-height:25px;
        text-transform: uppercase;
      }
    }
    &__img-col
    {
      padding: 0;
      line-height: 0;
      & img
      {
        width:100%;
      }
    }
    &.left
    {
      & .category__text
      {
        right:auto;
        left:0;
        padding-right: 0;
        padding-left: 200px;
      }
    }
  }
}


.special
{
  padding: 70px 0 50px;
  &.bottom
  {
    padding: 45px 0 50px;

    & .title
    {
      font-family:@title-font;
      font-weight: 500;
      line-height: 30px;
      text-transform: uppercase;
      font-size: 22px;
      margin: 0 0 50px;
      color: #2b3434;
      margin-top: 0;
      letter-spacing: 1.1px;
    }
  }
  & .h3-main
  {
    display: inline-block;
    margin-bottom: 42px;
  }
  &__tabs
  {
    display: inline-block;
    position: relative;
    top: -4px;
    margin-left: 25px;
    & li
    {
      display: inline-block;
      font-size: 11px;
      padding: 7px 23px;
      font-weight: 500;
      border-radius: 50px;
      text-transform: uppercase;
      color:#2b3434;
      margin-right: 3px;
      cursor: pointer;
      &.active
      {
        background: #2b3434;
        color:White;
      }
    }
  }
  & .price
  {
    color: white;
    font-size: 16px;
    font-weight: 700;
    margin: 0;
  }
  & .article
  {
    color: white;
    font-size: 16px;
    margin: 0;
    margin-bottom: 5px;
  }
  &__item
  {
    position: relative;
    line-height: 0;
    &:hover .special__overlay
    {
      opacity: 1;
    }
  }
  &__overlay
  {
    opacity: 0;
    transition: 0.3s all;
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    text-align: center;
    background: #000000a8;
    vertical-align: middle;
    padding-top: 43%;
  }
}

.instagram
{
  padding: 0 0 70px;
  &__link
  {
    display: block;
    position: relative;
    line-height: 0;
    &:after
    {
      content: '';
      width:100%;
      height:100%;
      position: absolute;
      background: rgba(0,0,0,0.8);
      transition: 0.3s;
      top:0;
      left:0;
      opacity:0;
    }
    &:hover
    {
      &:after
      {
        opacity:1;
      }
    }
  }
}