@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato/Lato-Black.ttf") format("truetype");
  font-style: normal;
  font-weight: 900;
}


@base-font:'Lato', sans-serif;
@title-font:  'Oswald', sans-serif;
@text-font: 'Lato', sans-serif;

body {
  font-family: @base-font;
  background: @bluepink-gradient;
  font-size: 18px;
  line-height: 1.4em;
  font-weight: 400;
}

a {
  color: inherit;
  text-decoration: none;
}

// .text{
& img {
  max-width: 100%;
}
.subtitle
{
  text-transform: uppercase;
  font-size: 12px;
  margin-top: -5px;
  color: #b1b0b0;
}
.arr-link
{
  text-transform: uppercase;
  font-size: 12px;
  line-height:normal;
  position: relative;
  &:after
  {
    width:41px;
    height:16px;
    right: -61px;
    top: -2px;
    position: absolute;
    content: '';
    background: url("../img/arr-r-r.png");
  }
}
h1 {
  font-family: @title-font;
  font-size: 30px;
  line-height: 40px;
  color:#2b3434;
  letter-spacing: 1.5px;
  font-weight: 500;
  margin-top: 40px;
  text-transform: uppercase;
  margin-bottom: 0;
  //&.main {
  //  font-family: @title-font;
  //  font-weight: 100;
  //  line-height: initial;
  //  font-size: 45px;
  //  position: relative;
  //  margin: 5px 0 45px;
  //  color: #18202a;
  //}
  &.in_block {
    display: inline-block;
  }
}

h2 {
  font-family: @title-font;
    font-weight: 500;
    letter-spacing: 1.5px;
    font-size: 30px;
    line-height: 25px;
    color: #2b3434;
    margin: 5px 9px 43px;

  &.h2-main {
    font-family: @title-font;
    font-weight: 500;
    letter-spacing: 1.2px;
    font-size: 32px;
    line-height: 50px;
    margin: 0;
    text-transform: uppercase;
  }
}
.section-title
{
  text-transform: uppercase;
  font-family: @base-font;
  font-size: 12px;
  line-height: 12px;
  font-weight: 300;
  color:#abaaaa;
  display: inline-block;
  margin-bottom: 10px;
}
h3 {
  font-family: @title-font;
  font-weight: 400;
  color:#2b3434;
  font-size: 25px;
  line-height:25px;
  margin-top: 18px;
  margin-bottom: 42px;
  letter-spacing: 1.2px;
  &.h3-main
  {
    color:#2b3434;
    font-size: 22px;
    line-height:40px;
    text-transform: uppercase;
    margin-top: 18px;
    margin-bottom: 20px;
    letter-spacing: 1.2px;
  }
}

h4 {
  font-family: @title-font;
  font-weight: 400;
  line-height: 30px;
  text-transform: uppercase;
  font-size: 22px;
  margin: 37px 0 25px;
  color:#2b3434;
}

h5 {
  font-size: 23px;
  line-height: 30px;
  margin: 15px 0 25px;
  font-weight: 400;
  color:#8d9fa7;
}

h6 {
  font-size: 14px;
  text-transform: uppercase;
  line-height: 25px;
  margin: 40px 0 20px;
  font-weight: 700;
  font-family: @base-font;
  color:#8d9fa7;
}

p,
li {
  font-size: 15px;
  line-height: 25px;
  // margin: 25px 0;
  color: #282e34;
}

li {
  font-size: 17px;
  line-height: 24px;
}

/*
p:first-child,
li:first-child,
hr:first-child{
  margin-top: 0;
} */
p:last-child,
li:last-child,
hr:last-child {
  margin-bottom: 0;
}

hr {
  margin: 25px 0;
  height: 1px;
  border: none;
  width: 100%;
  background: #e9e4dc;
  &.dotted {
    background: url(../img/sep.png) repeat-x;
  }
  // }
  .gallery {
    margin-bottom: 30px;
    &__wrap {
      margin: 60px 0 55px;
    }
    &__arrows {
      position: relative;
      width: 180px;
      left: 50%;
      margin-left: -90px;
      text-align: center;
    }
    &__prev {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      transform: rotate(-180deg);
      font-size: 13px;
      cursor: pointer;
      & span {
        position: relative;
        top: 1px;
      }
      &:hover {
        opacity: 0.85;
      }
    }
    &__next {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      font-size: 13px;
      cursor: pointer;
      & span {
        position: relative;
        top: 1px;
      }
      &:hover {
        opacity: 0.5;
      }
    }
    &__count {
      position: relative;
      display: inline-block;
      vertical-align: middle;
      font-family: @title-font;
      font-weight: 700;
      font-size: 25px;
      width: 100px;
    }
  }

  strong,
  b {
    font-weight: 700;
  }

  a {
    color: inherit;
    text-decoration: underline;

    &:hover {
      text-decoration: none;

    }
  }

  ul {
    padding-left: 0;
    margin: 20px 0;
    li {
      list-style: none;
      padding-left: 30px;
      position: relative;
      margin: 10px 0;
      &:before {
        content: "";
        position: absolute;
        height: 5px;
        width: 5px;
        top: 9px;
        left: 0;
        border-radius: 10px;
        // background: @blue;
      }

    }
  }

  ol {
    padding-left: 0;
    margin: 20px 0 20px 15px;
    li {
      list-style-position: outside;
      padding-left: 14px;
      position: relative;
      margin: 10px 0;
    }
  }

  table {
    width: 100%;
    margin: 20px 0;
    &.m--no_margin {
      margin-top: 0;
    }
    & thead {
      & td {
        font-size: 15px;
        font-weight: 700;
        color: #282e34;
        text-transform: uppercase;
        padding: 0 0 25px;
        border-bottom: 1px solid #282e34;
      }
    }
    & tbody {
      & td {
        border-bottom: 1px solid #e9e4dc;
        padding: 18px 0 18px 20px;
        font-size: 13px;
        &:first-child {
          padding-left: 0;
        }
      }
    }
    & tfoot {
      & td {
        border-bottom: 1px solid #e9e4dc;
      }
    }
    &.m--styled {
      margin: 45px 0 20px;
      thead td {
        font-size: 12px;
        text-transform: uppercase;
        font-weight: 900;
        background: #f4f1ed;
        padding: 10px 0 10px 20px;
        border-bottom: 0;
      }
      tbody td {
        border-bottom: 0;
        padding: 9px 0 9px 20px;
      }
      tbody tr {
        &:nth-child(even) {
          background: #f9f8f6;
        }
      }
    }
    &.cart tbody td {
      padding-left: 0;
    }
  }

}

@media screen and (max-width: 991px) {
  // .text{
  h1 {
    font-size: 30px;
    line-height: 40px;
  }

  h2 {
    font-size: 28px;
    line-height: 30px;
    margin-left: 30px;
    margin-bottom: 35px;
  }

  h3 {
    font-size: 26px;
    line-height: 25px;
    margin: 15px 0;
  }

  p,
  li {
    font-size: 14px;
    line-height: 23px;
    margin: 20px 0;
  }

  // }
}

@media screen and (max-width: 768px) {
  // .text{
  h1 {
    font-size: 30px;
    line-height: 40px;
    margin: 0;
    margin-bottom: 10px;
  }

  h1:before {
    left: -40px;
  }

  h2 {
    font-size: 28px;
    line-height: 30px;
    //margin-bottom: 35px;
    margin: 0 0 30px;
  }

  h3 {
    font-size: 26px;
    line-height: 25px;
  }

  p,
  li {
    font-size: 15px;
    line-height: 28px;
    margin: 16px 0;
    // }
  }
}
@media screen and (max-width: 425px) {
  h1 {
    font-size: 24px;
    line-height: 30px;
  }
  h3 {
    font-size: 22px;
    line-height: 25px;
  }
}